import { useEffect, useState } from 'react'
import { Popup, Button, Card, Badge } from 'antd-mobile'
import { useDispatch, useSelector } from 'react-redux';
import { selectDay } from '../../../../redux/slices/calendarSlice';

export default function Description(props) {
  const { timetable } = useSelector(state => state.calendar)
    const dispatch = useDispatch();
    // console.log(props.selected)
    const [visible4, setVisible4] = useState(false);
    useEffect(() => {
        if (props.selected) {
            setVisible4(true)
        }
        if (!props.selected) {
            setVisible4(false);
            dispatch(selectDay())
        }   
    }, [props])
    console.log(props.selected)
    if (!props.selected) {
      return null
    }
    const requestedTimeTable = timetable.filter(item => item.day === props.selected.day);
    console.log(requestedTimeTable)
    return (
            <Popup
              visible={visible4}
              onMaskClick={() => {
                setVisible4(false)
              }}
              bodyStyle={{ height: '40vh' }}
            >
              {requestedTimeTable[0] &&
              <Card>
                <div className='description'>
                  <div className='yoga'>
                  {<h4>{requestedTimeTable[0].training[0].name}</h4>}
                  {<p>
                    {requestedTimeTable[0].training[0].timePeriod[0].start + '-' + requestedTimeTable[0].training[0].timePeriod[0].end}<br/>
                    {requestedTimeTable[0].training[0].timePeriod[1].start + '-' + requestedTimeTable[0].training[0].timePeriod[1].end}<br/>
                    {requestedTimeTable[0].training[0].timePeriod[2].start + '-' + requestedTimeTable[0].training[0].timePeriod[2].end}<br/>
                    </p>}
                    </div>
                    <div className='dance'>
                  {<h4>{requestedTimeTable[0].training[1].name}</h4>}
                  {<p>
                    {requestedTimeTable[0].training[1].timePeriod[0].start + '-' + requestedTimeTable[0].training[1].timePeriod[0].end}<br/>
                    {requestedTimeTable[0].training[1].timePeriod[1].start + '-' + requestedTimeTable[0].training[1].timePeriod[1].end}<br/>
                    {requestedTimeTable[0].training[1].timePeriod[2].start + '-' + requestedTimeTable[0].training[1].timePeriod[2].end}<br/>
                    </p>}
                    </div>
                </div>
              </Card>}
            </Popup>
    )
}