import { SideBar } from "antd-mobile"
import { Link, NavLink } from "react-router-dom"

export default function NavigationBar() {
  const tabs = [
    {key: 1, title: 'Расписание', link: 'schedule'},
    {key: 2, title: 'Магазин', link: 'shop'},
    {key: 3, title: 'О нас', link: 'about'},
    {key: 4, title: 'Контакты', link: 'contacts'},
  ]
  return (
    <SideBar>
      {tabs.map(item => (
        <SideBar.Item key={item.key} title={<NavLink className='???????????????' to={`/yoga-dance-app/${item.link}`}>{item.title}</NavLink>} />
      ))}
    </SideBar>
  )
}