import { MenuUnfoldOutlined } from '@ant-design/icons';
import { Popup, Space, Button, Card } from 'antd-mobile'
import { useState } from 'react';
import NavigationBar from './NavigationBar';

export default function Menu() {
  const [visible3, setVisible3] = useState(false)

    return (
        <div className="menu">
            <Space>
                <Button
                  onClick={() => {
                    setVisible3(true)
                  }}
                >
                  <MenuUnfoldOutlined />
                </Button>
                <Popup
                  visible={visible3}
                  onMaskClick={() => {
                    setVisible3(false)
                  }}
                  position='left'
                  bodyStyle={{ width: '60vw', height: '60vh' }}
                >
                  {
                    <Card title='Меню'>
                      <NavigationBar />
                    </Card>
                  }
                </Popup>
            </Space>

        </div>
    )
}